<template >
  <div>
    <!-- <v-app-bar app src="https://picsum.photos/1920/1080?random"> </v-app-bar> -->
    <v-container style="max-width: 900px">
      <v-row justify="center">
        <v-col>
          <v-card class="mx-auto mb-0">
            <v-card-title class="mb-0"
              >QR kod ile temassz sra alma</v-card-title
            >
            <v-card-text> </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col>
          <v-card class="mx-auto mb-0">
            <v-card-title v-if="kisi && !mudurluk" class="mb-0"
              >Sayn {{ kisi.adi }} ltfen QR kodu okutunuz.</v-card-title
            >
             <v-card-title v-if="mudurluk" class="mb-0"
              >Sayn {{ kisi.adi }}  {{ mudurluk.adi }} ile nfus mdrlne hogeldiniz</v-card-title
            >
            <v-card-text>
              <!-- <div
                style="width: 300px; height: 300px; background-color: black"
                class="ma-auto"
              ></div> -->

              <div>
                <p class="error">{{ error }}</p>

               
                <qrcode-stream v-if="kisi && !mudurluk"  @decode="onDecode" @init="onInit" />

              </div>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import { QrcodeStream, QrcodeDropZone, QrcodeCapture } from "vue-qrcode-reader";
export default {
  components: {
    QrcodeStream,
    QrcodeDropZone,
    QrcodeCapture,
  },
  data: () => ({
    kisi: null,
    mudurluk:null,
    result: "",
    error: "",
  }),
  methods: {
    onDecode(result) {
     
    
      const url = new URL(result);
      var code = url.hash.replace("#/mqr/", "");
    
      var data = JSON.parse(window.atob(code));

      this.mudurluk = this.$store.state.ilceListesi.find((k) => k.id == data.id);
     
      
    },
    async onInit(promise) {
      try {
        await promise;
      } catch (error) {
        if (error.name === "NotAllowedError") {
          this.error = "ERROR: you need to grant camera access permisson";
        } else if (error.name === "NotFoundError") {
          this.error = "ERROR: no camera on this device";
        } else if (error.name === "NotSupportedError") {
          this.error = "ERROR: secure context required (HTTPS, localhost)";
        } else if (error.name === "NotReadableError") {
          this.error = "ERROR: is the camera already in use?";
        } else if (error.name === "OverconstrainedError") {
          this.error = "ERROR: installed cameras are not suitable";
        } else if (error.name === "StreamApiNotSupportedError") {
          this.error = "ERROR: Stream API is not supported in this browser";
        }
      }
    },
  },
  computed: {},
  mounted() {
    var kisiler = [
      { tc: 123, adi: "Hasan Kvlcm", randevu: true },
      { tc: 456, adi: "Mehmet Ayba", randevu: true },
      { tc: 789, adi: "Aye Trk", randevu: true },
      { tc: 321, adi: "Mahmut Kvlcm", randevu: false },
      { tc: 654, adi: "Emine Sur", randevu: false },
      { tc: 987, adi: "Ayfer Afakan", randevu: false },
    ];
    this.$store.commit("setKisiListesi", { data: kisiler });
    var ilceler = [
      {
        id: 10,
        adi: "Keiborlu",
        gecerlimi: true,
        t: new Date(),
        h: "1234567890yuiopasqwertdfghjklzxc",
      },
      {
        id: 11,
        adi: "Muratpaa",
        gecerlimi: true,
        t: new Date(),
        h: "123456uiertyhjklzqwxcopasdfg7890",
      },
      {
        id: 12,
        adi: "ankaya",
        gecerlimi: true,
        t: new Date(),
        h: "123459tyuio0qwerjklzxcp678asdfgh",
      },
      {
        id: 13,
        adi: "Yenimahalle",
        gecerlimi: false,
        t: new Date("2021-05-20T03:24:00"),
        h: "123tyuiop45678xc9erasdf0qwghjklz",
      },
    ];
    this.$store.commit("setIlceListesi", { data: ilceler });
    var code = this.$route.params.code;
    try {
      var data = JSON.parse(window.atob(code));
      console.log(data);
      if (data.tc) {
        this.kisi = this.$store.state.kisiListesi.find((k) => k.tc == data.tc);
      } else if (data.mid) {
        //
      }
    } catch (error) {
      console.log("hata");
    }
  },
};
</script>