<template >
  <div>
    <!-- <v-app-bar app src="https://picsum.photos/1920/1080?random"> </v-app-bar> -->
    <v-container style="max-width: 900px">
      <v-row>
        <v-col cols="12" md="12" sm="12">
          <v-alert
            border="left"
            colored-border
            color="grey"
            elevation="2"
            type="info"
            class="mb-0"
          >
            <h3 style="color: grey; text-align: center">
              Yeni mahalle nfus mdrlnde toplamda 3 kii iin 5 randevu
              oluturdunuz
            </h3>
            <h5 style="color: grey; text-align: center">
              Oluturduunuz randevular iin tarih ve saat seerek devam
              edebilirsiniz.
            </h5>
          </v-alert>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="12" md="4" sm="4">
          <v-card class="mx-auto mb-0">
            <v-list class="mb-0">
              <v-subheader>Adna ilem yapabileceiniz kiiler</v-subheader>

              <v-list-item-group color="primary" v-model="selectedPerson">
                <v-list-item
                  v-for="item in getKisiList"
                  :key="item.id"
                  @click="dialog = true"
                >
                  <v-list-item-avatar>
                    <v-icon class="grey lighten-1" dark> mdi-account </v-icon>
                  </v-list-item-avatar>

                  <v-list-item-content>
                    <v-list-item-title>{{ item.name }}</v-list-item-title>

                    <v-list-item-subtitle>{{
                      item.yakinlik
                    }}</v-list-item-subtitle>
                  </v-list-item-content>

                  <v-list-item-action> </v-list-item-action>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-card>
        </v-col>

        <v-col cols="12" md="8" sm="8">
          <v-alert
            v-if="types.length == 0"
            border="left"
            colored-border
            color="grey"
            elevation="2"
            type="info"
          >
            <h3 style="color: grey; text-align: center">
              Henz ekleme yapmadnz.
            </h3>
            <h5 style="color: grey; text-align: center">
              Kii ve ilem tipi seerek randevu oluturabilirsiniz.
            </h5>
          </v-alert>
          <v-card
            class="mx-auto mb-4"
            max-width="600"
            v-for="(item, i) in types"
            :key="i"
          >
            <div :ref="item.id">
              <v-card-title class="mb-0">
                <div style="width: 80%" @click="show(item)">
                  {{ item.person.name }}
                </div>

                <v-spacer></v-spacer>
                <v-btn color="red" @click="sil(item)" icon> SL </v-btn>
              </v-card-title>

              <v-card-subtitle @click="show(item)">
                {{ tipGoster(item.multiType) }}
              </v-card-subtitle>
              <div @click="show(item)">
                <v-card-text> 20.04.2021 11:30 </v-card-text>

                <v-card-actions>
                  <v-btn color="orange lighten-2" text> Saat se </v-btn>

                  <v-spacer></v-spacer>

                  <v-btn icon>
                    <v-icon>{{
                      showId == item.id ? "mdi-chevron-up" : "mdi-chevron-down"
                    }}</v-icon>
                  </v-btn>
                </v-card-actions>
              </div>
              <v-expand-transition>
                <div v-if="showId == item.id">
                  <v-divider></v-divider>

                  <v-row class="mt-1" justify="center" align="center">
                    <v-btn-toggle rounded class="mb-4 mt-2">
                      <v-btn icon elevation="2" medium>
                        <v-icon>mdi-chevron-left</v-icon>
                      </v-btn>
                      <v-btn text elevation="2" medium> 25.12.2021 </v-btn>
                      <v-btn icon elevation="2" medium>
                        <v-icon>mdi-chevron-right</v-icon>
                      </v-btn>
                    </v-btn-toggle>
                  </v-row>
                  <v-card-text>
                    <v-row
                      justify="center"
                      align="center"
                      style="max-height: 268px; overflow-y: scroll"
                    >
                      <v-col
                        v-for="(n, i) in times"
                        :key="i"
                        cols="auto"
                        class="pa-2"
                      >
                       
                        <v-btn text elevation="2" height="50" width="75">
                          {{ n.time }}
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </div>
              </v-expand-transition>
            </div>
          </v-card>
          <v-card elevation="4">
            <v-card-actions>
              <v-btn color="success" class="" @click="geri()"> GER </v-btn>
              <v-spacer></v-spacer>

              <v-btn color="success" class="" @click="ileri()"> LER </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <v-dialog v-model="dialog" max-width="360">
      <v-card class="mx-auto">
        <v-list>
          <v-subheader>Seilen mdrlkte yaplabilen ilemler</v-subheader>

          <v-list-item-group color="primary">
            <v-list-item
              v-for="item in uygunRandevular"
              :key="item.id"
              :disabled="item.density == 100"
              @click="ekle(item)"
            >
              <v-list-item-avatar>
                <v-icon class="grey lighten-1" dark> mdi-tag </v-icon>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>{{ item.name }}</v-list-item-title>

                <v-list-item-subtitle>{{
                  "Doluluk Oran %" + item.density
                }}</v-list-item-subtitle>
              </v-list-item-content>
              <v-list-item-action>
                <v-list-item-action-text
                  >En yakn tarih</v-list-item-action-text
                >
                <v-list-item-action-text>20.05.2021</v-list-item-action-text>
              </v-list-item-action>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
export default {
  data: () => ({
    dialog: false,
    showId: null,
    selectedPerson: null,
    uygunRandevular: [
      { id: 1, density: 60, name: "TCKK", groupId: 1 },
      { id: 2, density: 60, name: "Src", groupId: 1 },
      { id: 3, density: 88, name: "Pasaport", groupId: 1 },
      { id: 4, density: 60, name: "Adres", groupId: 2 },
      { id: 5, density: 100, name: "Doum", groupId: 3 },
      { id: 6, density: 80, name: "Dier lemler", groupId: 4 },
    ],

    times: [
      { time: "08:00" },
      { time: "08:15" },
      { time: "08:30" },
      { time: "08:45" },
      { time: "09:00" },
      { time: "09:15" },
      { time: "09:30" },
      { time: "09:45" },
      { time: "10:00" },
      { time: "10:15" },
      { time: "10:30" },
      { time: "10:45" },
      { time: "11:00" },
      { time: "11:15" },
      { time: "11:30" },
      { time: "11:45" },
      { time: "13:00" },
      { time: "13:15" },
      { time: "13:30" },
      { time: "13:45" },
      { time: "14:00" },
      { time: "14:15" },
      { time: "14:30" },
      { time: "14:45" },
      { time: "15:00" },
      { time: "15:15" },
      { time: "15:30" },
      { time: "15:45" },
      { time: "16:00" },
      { time: "16:15" },
      { time: "16:30" },
      { time: "16:45" },
    ],
  }),
  methods: {
    ileri() {
      var etkilenenler = this.$store.state.kisiListesi.filter((k) => k.islem);
      if (etkilenenler.length > 0) {
        this.$store.commit("setIslemKisiList", { data: etkilenenler });
        this.$store.commit("setSeciliKisiIndex", { data: 0 });

        //kisiekbilgi

        this.$router.push({ name: "kisiekbilgi", params: { index: 0 } });
      }
    },
    show(item) {
      //this.$refs[item.id].scrollIntoView({behavior: "smooth"});
      setTimeout(() => {
        this.$refs[item.id][0].scrollIntoView({
          behavior: "smooth",
          block: "center",
        });
      }, 200);
      this.showId =
        this.showId == item.id ? (this.showId = null) : (this.showId = item.id);
      this.$refs[item.id];
    },
    sil(item) {
      var kalanlar = this.$store.state.selectedTypes.filter(
        (k) => !(k.id === item.id && k.person.id === item.person.id)
      );
      this.$store.commit("setSelectedTypes", { data: kalanlar });
      var person = this.$store.state.selectedTypes.find(
        (k) => !(k.person.id === item.person.id)
      );
      if (person) {
        var kisilist = this.$store.state.kisiListesi;
        var p = kisilist.find((k) => k.id == person.person.id);
        if (p) {
          p.islem = false;
          this.$store.commit("setKisiListesi", { data: kisilist });
        }
      }
    },
    ekle(item) {
      if (this.selectedPerson != null && this.selectedPerson > -1) {
        var person = { ...this.$store.state.kisiListesi[this.selectedPerson] };

        var selectedTypes = this.$store.state.selectedTypes;
        var varmi = selectedTypes.find(
          (k) => k.person.id == person.id && k.typeGroupId == item.groupId
        );

        this.$store.state.kisiListesi[this.selectedPerson].islem = true;
        this.$store.commit("setKisiListesi", {
          data: this.$store.state.kisiListesi,
        });
        if (varmi) {
          if (!varmi.multiType.some((k) => k.id == item.id)) {
            varmi.multiType.push(item);
          }
          this.showId = varmi.id;
        } else {
          var newitem = {
            person: person,
            id: Math.floor(Math.random() * 100000),
            typeId: item.id,
            multiType: [item],
            typeGroupId: item.groupId,
            name: item.name,
          };
          this.showId = newitem.id;
          selectedTypes.push({ ...newitem });
        }

        setTimeout(() => {
          this.$refs[this.showId][0].scrollIntoView({
            behavior: "smooth",
            block: "center",
          });
        }, 500);
        this.$store.commit("setSelectedTypes", { data: selectedTypes });
      }
      this.dialog = false;
      this.selectedPerson = null;
    },
    tipGoster(types) {
      return types ? types.map((k) => k.name).toString() : [].toString();
    },
    geri() {
      this.$router.go(-1);
    },
  },
  computed: {
    types() {
      return this.$store.state.selectedTypes;
    },
    getKisiList() {
      return this.$store.state.kisiListesi;
    },
  },
  mounted() {
    var token = sessionStorage.getItem("token");
    if (!token) {
      this.$router.push({
        name: "kimlikdogrula",
        params: { returnpage: "randevual" },
      });
    }
    var kisiList =
      this.$store.state.kisiListesi.length == 0
        ? [
            { id: 1, yakinlik: "kendisi", name: "Kii 1" },
            { id: 2, yakinlik: "kz", name: "Kii 2" },
            { id: 3, yakinlik: "ei", name: "Kii 3" },
            { id: 4, yakinlik: "annesi", name: "Kii 4" },
            { id: 5, yakinlik: "babas", name: "Kii 5" },
          ]
        : this.$store.state.kisiListesi;
    this.$store.commit("setKisiListesi", { data: kisiList });
  },
};
</script>